<template>
    <div class="info">
        <div class="profile">
            <img :src="userdata.profile" alt="">
            <div id="select_img" ><button id="select_btn" @click="select_img()">修改头像</button></div>
            <div v-show="img_submit" class="img_btn"><button @click="submit_info('img')">保存</button><button @click="img_submit=false">取消</button></div>
        </div>
            <div class="informa">
                <p class="name">{{userdata.username}}</p>
                <table>
                <tbody>
                    <tr>
                        <td>性别：</td>
                        <td><span v-show="!editable" v-text="userdata.gender==1?'男':(userdata.gender==2?'女':'未知')"></span>
                            <span v-show="editable"><input type="radio" value=1 name='gender' id="gender1" v-model="pick" ><label for="gender1">男</label> &nbsp; <input type="radio" value=2 name='gender' id="gender2" v-model="pick"><label for="gender2">女</label></span>
                        </td>
                    </tr>
                    <tr>
                        <td><label for="location">所在地：</label></td>
                        <td><input type="text" name="location" id="location" :class="[editable?editing:unedit]" :disabled="!editable" :value="userdata.location" ref="location"></td>
                    </tr>
                    <tr>
                        <td><label for="self_intro">自我介绍：</label></td>
                        <td><input type="text" id="self_intro" name="self_intro" :class="[editable?editing:unedit]" :disabled="!editable" :value="userdata.intro" ref="intro"></td>
                    </tr>
                    <tr>
                        <td>注册时间：</td>
                        <td><span>{{userdata.rig_time}}</span></td>
                    </tr>
                    <tr>
                        <td>最近登录：</td>
                        <td><span>{{userdata.last_log}}</span></td>
                    </tr>
                    <input type="file" id="input_img" ref="input_img" accept="image/png,image/gif,image/jpeg">
                </tbody>
                <div v-show="editable" class="submit_cancel"><input type="button" value="保存"  class="submit" @click="submit_info('text')"><input type="button" value="取消"   class="cancel" @click="editable=false"></div>
                </table>
                <p class='edit_info' @click='editable=true'>编辑个人信息</p>
            </div>
        </div>
</template>


<script>
export default{
    props:{

    },
    data(){
        return{
            editable:false,
            editing:"editing",
            unedit:"unedit",
            userdata:{
                username:"初始值",
                profile:"image/user_profile/default_profile.png",
                gender:0,
                location:"初始值",
                rig_time:"1970-1-1",
                last_log:"1970-1-2",
                intro:"初始值"
                },
            pick:'',
            img_submit:false,
        };
    },
    methods:{
        submit_info(type){
            
            if(type=="text"){
                let formdata='';
                formdata+='type'+'='+type+'&';
                formdata+='gender'+'='+this.pick+'&';
                formdata+='location'+'='+this.$refs.location.value+'&';
                formdata+='intro'+'='+this.$refs.intro.value;
                console.log(formdata);
                this.editable=false;
                this.$axios.post("/user/data",formdata,{headers:{'Content-Type':'application/x-www-form-urlencoded'}})
                .then((res)=>{console.log(res);})
                .catch((err)=>{alert("修改失败"+err)});
            }
            else if(type=="img"){
                var formdata=new FormData();
                formdata.append('profile',this.$refs.input_img.files[0]);
                this.$axios.post("/user/data",formdata,{headers:{'Content-Type':'multipart/form-data'}})
                .then((res)=>{alert(res)})
                .catch((err)=>{alert("修改失败"+err)});
                this.img_submit=false;
            }            
            this.getUserData();
        },
        select_img(){
            this.img_submit=true;
            var img_btn=this.$refs.input_img;
            var event=document.createEvent("MouseEvents");
            event.initMouseEvent("click",true,true,document.defaultView);
            img_btn.dispatchEvent(event);
        },
        getUserData(){
            this.$axios.get("/user/data"+location.search)
            .then((response)=>{this.userdata=response.data;})
            .catch((error)=>{console.log(error)});
        }

    },
    created(){
        this.getUserData();
        this.pick=this.userdata.gender;
    }
}
</script>


<style scope>
.info{
	margin:30pt 100pt;
}
.profile{
	width:150pt;
	height:150pt;
	float:left;
	border:1px solid #999;
    position: relative;
}
.profile img{
	width:150pt;
	height:150pt;

}
#select_img{
    position:absolute;
    bottom:2px;
    left:48pt;
    
}
#select_btn{
    border:none;
    padding:0.1em 0.2em;
    outline: none;
    background:#999;
    color:#FFF;
    border-radius:3px;
    border:1px solid #999;
}
.img_btn{
    position: absolute;
    bottom:-20pt;
    left:45pt;
}
.img_btn button{
    margin-right:5pt;
}
#input_img{
    display:none;
}
.informa{
	position:relative;
	margin-left:200pt;
	width:400pt;
	height:150pt;
}
.name{
	font-size:16pt;
	background:-webkit-linear-gradient(0deg,rgb(230,235,255),rgb(200,200,255));
	background:linear-gradient(0deg,rgb(230,235,255),rgb(200,200,255));
}
tr td:first-of-type{
    color:#666;
}
tr{
    height: 1.5em;
}
.edit_info{
	position:absolute;
	bottom:0pt;
	right:0pt;
    border:1px solid #CDF;
    color:#09C;
    border-radius:3px;
    width:7em;
    height:1.5em;
    text-align: center;
    line-height: 1.5em;
    background: #EEF;
}
.edit_info:hover{
    background: #CDF;
}
.editing{
    width:200pt;
    font-size:1rem;
    border:none;
    outline:none;
    border-bottom:1px solid #CCC;
}
.editing:focus{
    border-bottom:1px solid #000;
}
.unedit{
    background:#FFF;
    color:#000;
    border:none;
    outline:none;
    font-size:1rem;
}
.submit_cancel{
    position: absolute;
    bottom:0;
    left:100pt;
}
.submit{
    background-color:#08F;
    font-size:1rem;
    color:#FFF;
    padding:0.3em;
    border:solid 1px #08F;
    border-radius:2px ;
}
.cancel{
    margin-left:20pt;
    font-size:1rem;
    padding:0.3em;
    background:#FFF;
    color:#666;
    border-radius:2px ;
    border:1px solid #666;
}
</style>